.boxSigno {
    background-color: #080808;
    color: #fff;
    margin: 25px 10px 0;
    padding: 5px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    border-radius: 5px;
    border: 1px solid #734136;
    width: 150px;
    height: 200px;
}

.imagemSigno {
    width: 140px;
    height: 100PX;
    background-color: #FFF;
    object-fit: contain;
    border-radius: 2PX;
    border: 1px solid #734136;
    box-sizing: border-box;
    margin-bottom: 10px;
    padding: 8px;
    flex: 1;
}

.nomeSigno {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 10px;
}

.periodoNascimento{
    font-size: 14px;
}